import './css/styles.css';
import {addLeaderboardItems, getLeaderboardItems} from './getItems.js';

window.onload = async () => {
  const submitItem = document.getElementById('submitLeaderboard');
  const refreshItem = document.getElementById('refresh');
  
  getLeaderboardItems();

  submitItem.addEventListener('submit', addLeaderboardItems);
  refreshItem.addEventListener('click', getLeaderboardItems);
};
